/* CSS Reset */

/* Remove default margin and padding */
body, h1, h2, h3, h4, h5, h6, p, ol, ul {
    margin: 0;
    padding: 0;
}

/* Set box-sizing to border-box for all elements */
* {
    box-sizing: border-box;
}

/* Remove list styles (bullets/numbers) */
ol, ul {
    list-style: none;
}

/* Remove default hyperlink styles */
a {
    text-decoration: none;
    color: inherit;
}

/* Reset form elements */
button, input, select, textarea {
    margin: 0;
    padding: 0;
    border: none;
    outline: none;
    font-family: inherit;
}

/* Additional resets can be added as needed */

/* End of CSS Reset */

/* Root CSS Element */
:root {
    --primary-color: #0ec60e;
    --primary-color1: #0ca30c;
    --secondary-color: #f6ff00;
    --accent-color: #ff3c00;
    --background-color: #251f2b;
    --background-color-invert: #a498b0;
    --text-color: #ededed;
    --text-color-invert: #2e2e2e;
    
    --font-family: 'Roboto', sans-serif;
}

html {
    width: 100vw;
    height: 100vh;
    background-color: var(--background-color);
}

body {
    font-family: var(--font-family);

    background-color: var(--background-color);
    color: var(--text-color);
    
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; 
}

h1 {
    font-size: 2rem;
    margin-bottom: 1rem;
}

#canvas-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 0.2rem;
}

canvas {
    border: 3px solid var(--text-color);
    margin: 0;
    padding: 0;
}

.input-container {
    display: flex;
    flex-direction: row;
    border: 2px solid rgb(159, 153, 153);
    border-radius: 0.25rem;
    margin-top: 1rem;
    width: 18rem;
    height: fit-content;
    color: var(--text-color);
    background-color: var(--background-color);
}

.input-container input {
    width: 14rem;
    height: fit-content;
    padding: 0.5rem;
    background-color: #12071c;
    color: var(--text-color);
    border: none;
    outline: none;
    font-family: var(--font-family);
}
.input-container button {
    width: 4rem;
    height: fit-content;
    background-color: var(--primary-color);
    border: none;
    outline: none;
    font-family: var(--font-family);
    color: var(--text-color);
    cursor: pointer;
}
.input-container button:hover {
    background-color: var(--primary-color1);
}

/* Main container for all bottom stuffs */
#bottom-container {
    display: flex;
    flex-direction: row;
    gap: 1rem;
    margin-top: 1rem;
    width: 800px;
}


#bottom-container > * {
    flex: 1; /* Each child will take equal width */
    height: 100%; /* Each child will fill the vertical space */
    margin: auto;
    display: flex;
    flex-direction: column; 
    box-sizing: border-box;
}

/* Divider line */
#bottom-container > *:first-child {
    border-right: 2px solid var(--text-color);
}

